'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Plus, Users, Settings } from 'lucide-react'
import GroupList from './GroupList'
import GroupView from './GroupView'
import CreateGroupModal from './CreateGroupModal'

export interface Group {
    id: string
    name: string
    description?: string
    created_by: string
    created_at: string
    updated_at: string
    member_count?: number
}

export default function GroupPanel() {
    const [groups, setGroups] = useState<Group[]>([])
    const [selectedGroup, setSelectedGroup] = useState<Group | null>(null)
    const [showCreateModal, setShowCreateModal] = useState(false)
    const [loading, setLoading] = useState(true)

    // Mock data for now - will be replaced with API calls
    useEffect(() => {
        // Simulate loading groups
        setTimeout(() => {
            setGroups([
                {
                    id: '1',
                    name: 'Family Health',
                    description: 'Health habits for the family',
                    created_by: 'user1',
                    created_at: new Date().toISOString(),
                    updated_at: new Date().toISOString(),
                    member_count: 3
                },
                {
                    id: '2',
                    name: 'Morning Routine',
                    description: 'Start the day right together',
                    created_by: 'user1',
                    created_at: new Date().toISOString(),
                    updated_at: new Date().toISOString(),
                    member_count: 2
                }
            ])
            setLoading(false)
        }, 1000)
    }, [])

    const handleCreateGroup = (newGroup: Group) => {
        setGroups(prev => [...prev, newGroup])
        setShowCreateModal(false)
    }

    const handleSelectGroup = (group: Group) => {
        setSelectedGroup(group)
    }

    const handleBackToGroups = () => {
        setSelectedGroup(null)
    }

    if (loading) {
        return (
            <div className="glass-card p-6">
                <div className="animate-pulse space-y-4">
                    <div className="h-4 bg-ocean-200 rounded w-3/4"></div>
                    <div className="h-4 bg-ocean-200 rounded w-1/2"></div>
                    <div className="h-4 bg-ocean-200 rounded w-2/3"></div>
                </div>
            </div>
        )
    }

    return (
        <div className="space-y-6">
            {!selectedGroup ? (
                <>
                    {/* Groups Overview */}
                    <div className="glass-card p-6">
                        <div className="flex items-center justify-between mb-4">
                            <div className="flex items-center space-x-2">
                                <Users className="w-5 h-5 text-ocean-600" />
                                <h3 className="text-lg font-semibold text-ocean-800">
                                    Your Groups
                                </h3>
                            </div>
                            <Button
                                onClick={() => setShowCreateModal(true)}
                                className="glass-button-sm flex items-center space-x-2"
                            >
                                <Plus className="w-4 h-4" />
                                <span>Create Group</span>
                            </Button>
                        </div>
                        
                        <GroupList 
                            groups={groups}
                            onSelectGroup={handleSelectGroup}
                        />
                    </div>
                </>
            ) : (
                <GroupView 
                    group={selectedGroup}
                    onBack={handleBackToGroups}
                />
            )}

            {/* Create Group Modal */}
            <CreateGroupModal
                isOpen={showCreateModal}
                onClose={() => setShowCreateModal(false)}
                onCreateGroup={handleCreateGroup}
            />
        </div>
    )
}
